import React, { useState, useRef } from "react";
import { Card, Form, Button } from "react-bootstrap";
import { AiOutlineEye, AiOutlineEyeInvisible } from "react-icons/ai";
import { useForm } from "react-hook-form";
import { changePassword } from "../../../app/features/auth/auth.slice";
import { useDispatch } from "react-redux";

function Settings() {
  const dispatch = useDispatch();
  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const [newPassword, setNewPassword] = useState(false);
  const handleClickNewPassword = () => setNewPassword((show) => !show);

  const [confirmPassword, setConfirmPassword] = useState(false);
  const handleClickConfirmPassword = () => setConfirmPassword((show) => !show);

  const {
    register,
    watch,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  const password = useRef({});
  password.current = watch("password", "");

  const moveToNext = () => {
    reset();
  };
  function onSubmitConfirmPassword(data) {
    const finalData = {
      password: data.oldPassword,
      newPassword: data.password,
    };
    dispatch(changePassword({ finalData, moveToNext }));
  }

  return (
    <div className="d-flex justify-content-center">
      <Card className="account__settings-layout p-5">
        <Card.Body className="p-0">
          <Card.Title>Account Setting</Card.Title>
          <h5 className="mt-3 font-bold">Change password</h5>
          <p className="mt-3 password-description">
            Use a strong password. Don&apos;t use a password from another sites,
            or something too obvious like your pet&apos;s name.
          </p>
          <Form
            className="mt-5 d-flex justify-content-center flex-column"
            onSubmit={handleSubmit(onSubmitConfirmPassword)}
          >
            <Form.Group
              className="mb-3 position-relative"
              controlId="adminOldPass"
            >
              <Form.Label className="fw-bold">Old Password</Form.Label>
              <div className="">
                <Form.Control
                  type={showPassword ? "text" : "password"}
                  placeholder="Password"
                  name="oldPassword"
                  size="lg"
                  {...register("oldPassword", {
                    required: true,
                  })}
                />
                {errors.oldPassword && (
                  <p className="text-danger">{errors.oldPassword.message}</p>
                )}
                <div onClick={handleClickShowPassword} className="eye-icon">
                  {showPassword ? (
                    <AiOutlineEye size={18} />
                  ) : (
                    <AiOutlineEyeInvisible size={18} />
                  )}
                </div>
              </div>
            </Form.Group>

            <Form.Group
              className="mb-4 position-relative"
              controlId="adminNewPass"
            >
              <Form.Label className="fw-bold Form-labeling">
                New Password
              </Form.Label>
              <Form.Control
                type={newPassword ? "text" : "password"}
                placeholder="Password"
                size="lg"
                name="password"
                {...register("password", {
                  required: true,
                })}
              />
              {errors.password && (
                <p className="text-danger">{errors.password.message}</p>
              )}
              <div onClick={handleClickNewPassword} className="eye-icon">
                {newPassword ? (
                  <AiOutlineEye size={18} />
                ) : (
                  <AiOutlineEyeInvisible size={18} />
                )}
              </div>
            </Form.Group>
            <Form.Group
              className="mb-4 position-relative"
              controlId="adminConfirmPass"
            >
              <Form.Label className="fw-bold">Confirm Password</Form.Label>
              <Form.Control
                className="Field-Sizing"
                type={confirmPassword ? "text" : "password"}
                placeholder="Password"
                name="confirmPassword"
                size="lg"
                {...register("confirmPassword", {
                  validate: (value) =>
                    value === password.current ||
                    "The passwords does not match",
                })}
              />
              {errors.confirmPassword && (
                <p className="text-danger">{errors.confirmPassword.message}</p>
              )}
              <div onClick={handleClickConfirmPassword} className="eye-icon">
                {confirmPassword ? (
                  <AiOutlineEye size={18} />
                ) : (
                  <AiOutlineEyeInvisible size={18} />
                )}
              </div>
            </Form.Group>
            <div className="d-grid gap-2 setting-save-button">
              <Button
                variant="primary"
                size="md"
                className="Save-password-button mt-3"
                type="submit"
              >
                Save Password
              </Button>
            </div>
          </Form>
        </Card.Body>
      </Card>
    </div>
  );
}

export default Settings;
